<template>
  <Loading :active="isLoading"></Loading>
  <div class="text-end mt-3">
    <button class="btn btn-primary" type="button"
    @click="openModal(true)">
      增加一個產品
    </button>
  </div>

  <table class="table mt-4">
    <thead>
      <tr>
        <th width="100">分類</th>
        <th width="50">產品名稱</th>
        <th width="50">圖片</th>
        <th width="80">產品描述</th>
        <th width="150">說明</th>
        <th width="120">原價</th>
        <th width="120">售價</th>
        <th width="100">是否啟用</th>
        <th width="200">編輯</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="item in products" :key="item.id">
        <td>{{ item.category }}</td>
        <td>{{ item.title }}</td>
        <td>
          <img :src="item.imageUrl" alt="商品照片" width="100" height="100">
        </td>
        <td>{{ item.description }}</td>
        <td>{{ item.content }}</td>
        <td class="text-right">
          {{ $filters.currency(item.origin_price) }}
        </td>
        <td class="text-right">
          {{ $filters.currency(item.price) }}
        </td>
        <td>
          <span class="text-success" v-if="item.is_enabled">啟用</span>
          <span class="text-muted" v-else>未啟用</span>
        </td>
        <td>
          <div class="btn-group">
            <button class="btn btn-outline-primary btn-sm"
            @click="openModal(false, item)">編輯</button>
            <button class="btn btn-outline-danger btn-sm"
            @click="openDelProductModal(item)">刪除</button>
          </div>
        </td>
      </tr>
    </tbody>
  </table>
  <!-- @emit-pages="getProducts" emit-pages觸發後執行getProducts -->
  <Pagination
    :pages="pagination"
    @emit-pages="getProducts">
  </Pagination>

  <ProductModal
    ref="productModal"
    :product="tempProduct"
    @update-product="updateProduct">
  </ProductModal>

  <DelModal
    :item="tempProduct"
    ref="delModal"
    @del-item="delProduct"/>
  </template>

<script>
import ProductModal from '@/components/ProductModal.vue'
import Pagination from '@/components/Pagination.vue'
import DelModal from '@/components/DelModal.vue'

export default {
  data () {
    return {
      products: [], // 所有產品資訊
      pagination: {}, // 分頁資訊
      // 先傳給ProductModal，ProductModal接收後儲存起來
      tempProduct: {}, // 主要為了編輯
      isNew: false, // 是否新增
      isLoading: false
    }
  },
  // 區域註冊
  components: {
    ProductModal,
    DelModal,
    Pagination
  },
  inject: ['emitter'],
  methods: {
    // 在生命週期觸發
    getProducts (page = 1) {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/products/?page=${page}`
      this.isLoading = true
      // 不需要加入資料 get(路徑)就可以了
      this.$http.get(api).then((res) => {
        this.isLoading = false
        if (res.data.success) {
          console.log(1, res.data)
          this.products = res.data.products
          this.pagination = res.data.pagination
        }
      })
    },
    // 新增(isNew)，tempProduct清空
    // 編輯(!isNew)，tempProduct = item
    openModal (isNew, item) {
      if (isNew) {
        this.tempProduct = {}
      } else {
        this.tempProduct = { ...item }
      }
      this.isNew = isNew
      const productComponent = this.$refs.productModal
      productComponent.showModal()
    },
    // 把外層資料儲存下來，再傳送到遠端
    updateProduct (item) {
      // console.log(item)
      this.tempProduct = item
      // 新增
      let api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product`
      let httpMethod = 'post'
      // 編輯
      if (!this.isNew) {
        api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${item.id}`
        httpMethod = 'put'
      }
      const productComponent = this.$refs.productModal
      this.$http[httpMethod](api, { data: this.tempProduct }).then((response) => {
        console.log(response)
        productComponent.hideModal()
        if (response.data.success) {
          this.getProducts()
          this.emitter.emit('push-message', {
            style: 'success',
            title: '更新成功'
          })
        } else {
          this.emitter.emit('push-message', {
            style: 'danger',
            title: '更新失敗',
            content: response.data.message.join('、')
          })
        }
      })
    },
    // 開啟刪除 Modal
    openDelProductModal (item) {
      this.tempProduct = { ...item }
      const delComponent = this.$refs.delModal
      delComponent.showModal()
    },
    delProduct () {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${this.tempProduct.id}`
      this.$http.delete(url).then((response) => {
        console.log(response.data)
        const delComponent = this.$refs.delModal
        delComponent.hideModal()
        this.getProducts()
      })
    }
  },
  created () {
    this.getProducts()
  }
}
</script>
